
import React from 'react';
import {useState, useEffect, createContext, useContext} from "react";
import {Navbar, Container, Nav, NavDropdown, Form, FormControl, Button} from 'react-bootstrap';
import PropTypes from 'prop-types';

function NavigationBar({dark=false, brandName="Brand Name", left, right }){
    return(
    <Navbar bg={dark?'dark':'light'} variant={dark?'dark':'light'} expand="lg">
      <Container fluid>
        <Navbar.Brand href="#">{brandName}</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />

        <Navbar.Collapse>

          <Nav className="me-auto my-2 my-lg-0" style={{ maxHeight: '100px' }} navbarScroll>
            {left? left.Children:''}
          </Nav>

          {/* rightside  */}
          {right? comp.right.Children:''}
          <Form className="d-flex">
            <FormControl type="search" placeholder="Search" className="me-2" aria-label="Search"/>
            <Button variant="outline-success">Search</Button>
          </Form>
          
        </Navbar.Collapse>
      </Container>
    </Navbar>
    )
}

NavigationBar.propTypes={
  dark: PropTypes.bool,
  brandName: PropTypes.string,
  left: PropTypes.element, 
  right: PropTypes.element
}


function NavItems ({lable, onClick}){
  return <Nav.Link onClick={onClick}>{lable}</Nav.Link>
}

NavItems.prototype={
  lable:PropTypes.string,
  onClick:PropTypes.func,
}

function DropDownItem({lable}){
  return(
    <NavDropdown>
      {lable.map(v=>{
        return  <NavDropdown.Item onClick={onClick}>{v.lable}</NavDropdown.Item>
      })}
    </NavDropdown>
  )
}

DropDownItem.prototype={
  lable:PropTypes.object,
  onClick:PropTypes.func,
}

export {NavigationBar, NavItems, }